// Menu.js

import React from "react";
import { Link } from 'react-router-dom';
import "./css/Menu.css";
import Menu_logo from './images/Menu_Pi-t.png';

const Menu = () => {
    const handleLogout = async () => {
        try {
            // POST  .
            const response = await fetch('/logout', {
                method: 'POST',  // HTTP POST  
                headers: {
                    'Content-Type': 'application/json',
                    //          .
                },
                // body   POST     .
                // : JSON.stringify({ key: 'value' })
            });

            //  
            if (response.ok) {
                console.log(' ');
                //     
                window.location.reload();
            } else {
                console.error(' ');
                //      
            }
        } catch (error) {
            console.error(' :', error);
            //      
        }
    };

    return (
        <div>
            <div className="Menu_bar">
                <div className="img">
                    <Link to="/Main">
                        <img className="Menu_logo" src={Menu_logo} />
                    </Link>
                </div>
                <h4 className="item">
                    <Link to="/Main" className="Menu_Link_style"></Link>
                </h4>
                <h4 className="item">
                    <Link to="/Iot" className="Menu_Link_style"></Link>
                </h4>
                <h4 className="item">
                    <Link to="/MyPage" className="Menu_Link_style"></Link>
                </h4>
                <h4 className="item">
                    <Link to="/Ids" className="Menu_Link_style"></Link>
                </h4>
                <h6 className="item_" onClick={handleLogout}>
                    <Link to="/" className="Menu_Link_style2"></Link>
                </h6>
            </div>
        </div>
    );
};

export default Menu;
