import React from 'react';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import Menu from './Menu';
import MyPage_1 from './css/MyPage_1.css';
import modifying_info from './images/modifying_info.png'


function Mypage_1() {
    const [password, setPassword] = useState('');
    const [newpassword, setnewPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');

    return (
        <body class="Main_body">
        <div>
            <Menu />
        </div>

        <div class='box'>
            <img className='modifying_info' src={modifying_info} />

            <div className='MyPage_input_box'>
                <input
                    type="password"
                    placeholder="  "
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                />
                <input
                    type="newpassword"
                    placeholder="  "
                    value={newpassword}
                    onChange={(e) => setnewPassword(e.target.value)}
                />

                <input
                    type="password"
                    placeholder="  "
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                />

                <button class="true_button" onClick={() => alert('.')}></button>
                <Link to="/Mypage">
                    <button class="false_button"></button>
                </Link>

            </div>
        </div>
        </body>

    );
}


export default Mypage_1;