import React, { useState } from 'react';
import axios from 'axios';
import {useNavigate} from "react-router-dom";
import "./css/Login.css";
import welcome from './images/welcome.png';
import Swal from "sweetalert2";
import { Link } from 'react-router-dom';

const Login = () => {
    const [loginInfo, setLoginInfo] = useState({
        email: '',
        password: '',
    });

    const navigate = useNavigate();

    const handleInputChange = (e) => {
        setLoginInfo({ ...loginInfo, [e.target.name]: e.target.value });
    };

    const handleLogin = () => {
        axios.post('http://43.203.119.177:8080/login', loginInfo)
            .then(response => {
                const token = response.data;
                localStorage.setItem('token', token);
                Swal.fire({
                    title: " !",
                    text: " :)",
                    icon: "success",
                    confirmButtonColor: "#007BBE",
                    confirmButtonText: ""
                });
                navigate('/Main');
                //     
            })
            .catch(error => {
                console.error(error.response.data);
                Swal.fire({
                    title: " ",
                    text: " ",
                    icon: "warning",
                    confirmButtonColor: "#007BBE",
                    confirmButtonText: ""
                })
                //      
            });
    };

    return (

        <body className='Login_body'>
        <div className='box'>
            <img className="welcome" src={welcome} />

            <div className='login_input_box'>
                <input type="email" name="email" placeholder=" ." onChange={handleInputChange} />
                <input type="password" name="password" placeholder=" " onChange={handleInputChange} />
            </div>

            <div class='login_box'>
                    <button class="login_button" onClick={handleLogin}></button>
            </div>

        </div>
        </body>
);
};

export default Login;
