import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";
import { Link } from 'react-router-dom';
import axios from 'axios';

import "./css/SignUp.css";
import welcome from './images/welcome.png'
import Swal from "sweetalert2";

const Register = () => {
    const navigate = useNavigate();

    const [user, setUser] = useState({
        name: '',
        email: '',
        password: '',
        confirmPassword: '',
    });

    const [passwordsMatch, setPasswordsMatch] = useState(true);

    const handleInputChange = (e) => {
        setUser({ ...user, [e.target.name]: e.target.value });
        setPasswordsMatch(true);
    };

    const handleRegister = () => {
        if (user.password !== user.confirmPassword) {
            setPasswordsMatch(false);
            Swal.fire({
                title: "  ",
                text: " ",
                icon: "warning",
                confirmButtonColor: "#007BBE",
                confirmButtonText: ""
            });
            return;
        }

        axios.post('http://43.203.119.177:8080/register', user)
            .then(response => {
                console.log(response.data);
                Swal.fire({
                    title: " !",
                    text: " :)",
                    icon: "success",
                    confirmButtonColor: "#007BBE",
                    confirmButtonText: ""
                });
                navigate('/login');
            })
            .catch(error => {
                console.error(error.response.data);
                Swal.fire({
                    title: " .",
                    icon: "error",
                    confirmButtonColor: "#007BBE",
                    confirmButtonText: ""
                });
            });
    };

    //     
    const handleCancel = () => {
        //        
        setPasswordsMatch(true);
        // FirstPage 
        navigate('/');
    };

    return (
        <div className='box'>
            <img className="welcome" src={welcome} />

            <div className='signup_input_box'>
                <input type="name" name="name" placeholder=" " onChange={handleInputChange} />
                <input type="email" name="email" placeholder=" " onChange={handleInputChange} />
                <input type="password" name="password" placeholder=" " onChange={handleInputChange} />
                <input type="password" name="confirmPassword" placeholder=" " onChange={handleInputChange} />
            </div>


            <div className='signup_box'>
                <button className="true_button" onClick={handleRegister}></button>
                {/*      handleCancel   */}
                <button className="false_button" onClick={handleCancel}></button>
            </div>
        </div>
    );
};

export default Register;


