import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import CameraStream from './CameraStream'; //  import 

function Camera2() {
    const [isCamera2On, setCamera2On] = useState(false); //     

    const handleOn2 = async () => {
        try {
            const response = await axios.post('     API ');
            console.log(response.data); //   
            setCamera2On(true); //       isCameraOn  true 
        } catch (error) {
            console.error(error);
        }
    };

    const handleOff2 = async () => {
        try {
            const response = await axios.post('     API ');
            console.log(response.data); //   
            setCamera2On(false); //       isCameraOn  false 
        } catch (error) {
            console.error(error);
        }
    };


    return (
        <div>
            <h1>2</h1>


                <button onClick={handleOn2}>Camera 2 ON</button>
                <button onClick={handleOff2}>Camera 2 OFF</button>
                {isCamera2On && <CameraStream />}

            {/*isCameraOn  true  CameraStream  */}

        </div>
    );
}

export default Camera2;